export const A_star = async function(graph, src, dest) {
  var ToAnimate = [];

  var width = this.board.matrix[0].length;

  var V = graph.length;

  var dist = new Array(V);
  var sptSet = new Array(V);
  var parent = new Array(V);
  var h = new Array(V);

  var x_dest = Math.floor(dest % width);
  var y_dest = Math.floor(dest / width);

  for (var i = 0; i < V; i++) {
    parent[i] = -1;
    dist[i] = Number.MAX_SAFE_INTEGER;
    sptSet[i] = false;
    var x = Math.floor(i % width);
    var y = Math.floor(i / width);
    h[i] = distance(x_dest, y_dest, x, y);
  }

  dist[src] = 0;

  for (var count = 0; count < V - 1; count++) {
    var u = MinF(dist, sptSet, V, h);
    if (u == -1) {
      break;
    }
    if (u == dest) {
      break;
    }
    sptSet[u] = true;
    if (u != src) {
      ToAnimate.push([Math.floor(u / width), u % width]);
    }
    for (var v = 0; v < V; v++) {
      var u_x = Math.floor(u % width);
      var u_y = Math.floor(u / width);
      var v_x = Math.floor(v % width);
      var v_y = Math.floor(v / width);
      if (
        !(
          (u_x == v_x + 1 && u_y == v_y + 1) ||
          (u_x == v_x - 1 && u_y == v_y + 1) ||
          (u_x == v_x + 1 && u_y == v_y - 1) ||
          (u_x == v_x - 1 && u_y == v_y - 1)
        ) &&
        !sptSet[v] &&
        graph[u][v] != 0 &&
        dist[u] != Number.MAX_SAFE_INTEGER &&
        dist[u] + graph[u][v] < dist[v]
      ) {
        parent[v] = u;
        dist[v] = dist[u] + graph[u][v];
      }
    }
  }
  await this.Animate_Visited(this.board.matrix, ToAnimate)
    .then(() => {
      return this.printPath(this.board.matrix, parent, dest);
    })
    .finally(() => {
      return new Promise(function(resolve) {
        resolve();
      });
    });
};

const distance = function(x1, y1, x2, y2) {
  //Returns the Manhatan distance
  return Math.abs(x1 - x2) + Math.abs(y1 - y2);
};

const MinF = function(dist, sptSet, V, h) {
  var min = Number.MAX_SAFE_INTEGER;
  var min_index = -1;
  for (var v = 0; v < V; v++) {
    if (sptSet[v] == false && dist[v] + h[v] < min) {
      min = dist[v] + h[v];
      min_index = v;
    }
  }
  return min_index;
};
