export const Board = function(x, y, x_pos_o, x_pos_d, y_pos) {
  this.x = x;
  this.y = y;
  this.matrix = new Array(y);
  for (var i = 0; i < y; i++) {
    this.matrix[i] = new Array(x);
    for (var j = 0; j < x; j++) {
      this.matrix[i][j] = {
        Empty: true,
        Origin: false,
        Destination: false,
        Wall: false,
        Visited: false,
        Path: false,
      };
    }
  }
  this.matrix[y_pos][x_pos_o].Origin = true;
  this.matrix[y_pos][x_pos_o].Empty = false;
  this.matrix[y_pos][x_pos_d].Destination = true;
  this.matrix[y_pos][x_pos_d].Empty = false;
};

export const Create_Matrix = function(p, a) {
  a -= 1;
  var width = Math.floor((window.innerWidth - 2 * p) / a);
  var height = Math.floor(Math.floor((window.innerHeight - 94 - 2 * p) / a));
  var y_pos = Math.floor(height / 2);
  var x_pos_o = Math.floor(width / 3);
  var x_pos_d = Math.floor((2 * width) / 3);
  var b = new this.Board(width, height, x_pos_o, x_pos_d, y_pos);
  return b;
};

export const FindSrc = function(graph) {
  for (var i = 0; i < graph.length; i++) {
    for (var j = 0; j < graph[i].length; j++) {
      if (graph[i][j].Origin) {
        return i * graph[0].length + j;
      }
    }
  }
};
export const FindDest = function(graph) {
  for (var i = 0; i < graph.length; i++) {
    for (var j = 0; j < graph[i].length; j++) {
      if (graph[i][j].Destination) {
        return i * graph[0].length + j;
      }
    }
  }
};
export const Create_Adj_Matrix = function(board) {
  var width = board[0].length;
  var height = board.length;
  var s = height * width;
  var matrix = new Array(s);
  for (var k = 0; k < s; k++) {
    matrix[k] = new Array(s);
    matrix[k].fill(0);
  }
  for (var i = 0; i < height; i++) {
    for (var j = 0; j < width; j++) {
      var current = board[i][j];
      if (current.Wall) {
        continue;
      } else {
        //Top
        if (i - 1 >= 0) {
          if (board[i - 1][j].Wall == false) {
            matrix[i * width + j][(i - 1) * width + j] = 1;
          }
        }
        //Bottom
        if (i + 1 < height) {
          if (board[i + 1][j].Wall == false) {
            matrix[i * width + j][(i + 1) * width + j] = 1;
          }
        }
        //Left
        if (j - 1 >= 0) {
          if (board[i][j - 1].Wall == false) {
            matrix[i * width + j][i * width + j - 1] = 1;
          }
        }
        //Right
        if (j + 1 < width) {
          if (board[i][j + 1].Wall == false) {
            matrix[i * width + j][i * width + j + 1] = 1;
          }
        }
        //Top-Left
        if (i - 1 >= 0 && j - 1 >= 0) {
          if (board[i - 1][j - 1].Wall == false) {
            matrix[i * width + j][(i - 1) * width + j - 1] = 1;
          }
        }
        //Top-Right
        if (i - 1 >= 0 && j + 1 < width) {
          if (board[i - 1][j + 1].Wall == false) {
            matrix[i * width + j][(i - 1) * width + j + 1] = 1;
          }
        }
        //Bottom-Left
        if (i + 1 < height && j - 1 >= 0) {
          if (board[i + 1][j - 1].Wall == false) {
            matrix[i * width + j][(i + 1) * width + j - 1] = 1;
          }
        }
        //Bottom-Right
        if (i + 1 < height && j + 1 < width) {
          if (board[i + 1][j + 1].Wall == false) {
            matrix[i * width + j][(i + 1) * width + j + 1] = 1;
          }
        }
      }
    }
  }
  return matrix;
};
