export const DFS = async function(
  graph,
  start,
  end,
  board = this.board.matrix
) {
  var ToAnimate = [];
  var width = board[0].length;
  var height = board.length;
  var size = graph.length;
  var visited = new Array(size);
  visited.fill(false);
  visited[start] = true;
  var i = 0;

  var stack = [start];

  var current = start;

  while (current != end) {
    current = stack.pop();
    ToAnimate.push([Math.floor(current / width), current % width]);
    var adj = [];
    var current_x = Math.floor(current % width);
    var current_y = Math.floor(current / width);
    if (current_x > 0) {
      if (board[current_y][current_x - 1].Wall == false) {
        adj.push(current - 1);
      }
    }
    if (current_x < width - 1) {
      if (board[current_y][current_x + 1].Wall == false) {
        adj.push(current + 1);
      }
    }
    if (current_y > 0) {
      if (board[current_y - 1][current_x].Wall == false) {
        adj.push(current - width);
      }
    }
    if (current_y < height - 1) {
      if (board[current_y + 1][current_x].Wall == false) {
        adj.push(current + width);
      }
    }
    for (i = 0; i < adj.length; i++) {
      if (visited[adj[i]] == false) {
        visited[adj[i]] = true;
        stack.push(adj[i]);
      }
    }
  }
  await this.Animate_Visited(board, ToAnimate).finally(() => {
    return new Promise(function(resolve) {
      resolve();
    });
  });
};
